

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
name: 'tabPage',
props: {
title: {
type: String,
default: '标题信息'
},
data: {
type: Array,
default: []
}
},
data () {
return {
idx: 0
}
},
computed: {
tabItem () {
let arr = this.data.map(item => {
if (!item.datas.length) {
return
}
return item.name
}).filter(item => !!item)
return arr
},
tabSelected () {
return this.tabItem[0]
},
tabContent () {

return this.data
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem
},
methods: {
selectItem (item) {
this.$emit('select', item)
},
setmessage (index) {
this.idx = index
this.$emit('on-setmessage', this.tabItem[index])
}
}
}



