
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: 'hotspot',
props: {
list: {
type: Array,
default () {
return []
}
},
title: {
type: String,
default: '最新热点'
}
},
components: {
Cell,
Group
},
methods: {
selectItem (item) {
this.$emit('select', item)
}
}
}



